  methods: {
    // 下载
    download(file){
      window.open(`${file}`)
    },
    // 初始化
    init(id,type) {
      if (id) {
        this.id = id;
        this.type = type;
      }
      if(this.type=='info'||this.type=='else'){
        this.info(id);
      }else if(this.type=='logistics'){
        this.logistics=false;
        this.info(id);
      }else if(this.type=='cross'){
        var obj = this.$storage.getObj('crossObj');
        for (var o in obj){
          if(o=='baoxiushebei'){
            this.ruleForm.baoxiushebei = obj[o];
	    this.ro.baoxiushebei = true;
            continue;
          }
          if(o=='yezhuhao'){
            this.ruleForm.yezhuhao = obj[o];
	    this.ro.yezhuhao = true;
            continue;
          }
          if(o=='yezhuxingming'){
