<script>

import canvasBg from "@/assets/js/canvas-bg-2.js";
import "@/assets/css/canvas-bg-2.css"

export default {
  data() {
    return {
      ruleForm: {
      },
      tableName:"",
      rules: {},
    };
  },
  mounted(){
    let table = this.$storage.get("loginTable");
    this.tableName = table;
        this.$nextTick(() => {
      canvasBg()
    })
      },
  created() {
    
  },
  methods: {
    // 获取uuid
    getUUID () {
      return new Date().getTime();
    },
    close(){
	this.$router.push({ path: "/login" });
    },
    // 注册
    login() {
	var url=this.tableName+"/register";
      if((!this.ruleForm.yezhuhao) && `yezhu` == this.tableName){
        this.$message.error(`业主号不能为空`);
        return
      }
      if((!this.ruleForm.mima) && `yezhu` == this.tableName){
        this.$message.error(`密码不能为空`);
        return
      }
      if((this.ruleForm.mima!=this.ruleForm.mima2) && `yezhu` == this.tableName){
	    this.$message.error(`两次密码输入不一致`);
	    return
      }
      if(`yezhu` == this.tableName && this.ruleForm.yezhushouji&&(!this.$validate.isMobile(this.ruleForm.yezhushouji))){
        this.$message.error(`业主手机应输入手机格式`);
        return
      }
      if(`yezhu` == this.tableName && this.ruleForm.shenfenzheng&&(!this.$validate.checkIdCard(this.ruleForm.shenfenzheng))){
        this.$message.error(`身份证应输入身份证格式`);
        return
      }
      if((!this.ruleForm.weixiugonghao) && `weixiudanwei` == this.tableName){
        this.$message.error(`维修工号不能为空`);
        return
      }
      if((!this.ruleForm.mima) && `weixiudanwei` == this.tableName){
        this.$message.error(`密码不能为空`);
        return
      }
      if((this.ruleForm.mima!=this.ruleForm.mima2) && `weixiudanwei` == this.tableName){
	    this.$message.error(`两次密码输入不一致`);
	    return
      }
      if(`weixiudanwei` == this.tableName && this.ruleForm.shifushouji&&(!this.$validate.isMobile(this.ruleForm.shifushouji))){
        this.$message.error(`师傅手机应输入手机格式`);
        return
      }
      this.$http({
        url: url,
        method: "post",
        data:this.ruleForm
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.$message({
            message: "注册成功",
            type: "success",
            duration: 1500,
            onClose: () => {
              this.$router.replace({ path: "/login" });
            }
          });
        } else {
          this.$message.error(data.msg);
        }
      });
    }
  }
};
</script>
